import { useCallback, useEffect, useLayoutEffect, useReducer, useRef, useState } from 'react'

import reducer, { ActionTypes } from '../reducers/reducer'
import { generateShapeElement, generateTextElement, getScaledElement, getShiftedElement } from '../utils/generateElement'
import { DrawingForms, Point, Settings, DrawableElement, CursorMode, Actions, Vertices, Scale, WhiteboardProps } from '../types/typing'
import { getUndoRedoHandler } from '../utils/shortcuts'
import { getElementAtPosition, isOnVertex } from '../utils/getElementAtPosition'
import { resizeCanvasFigure } from '../utils/resize'
import { drawElementsOnCanvas } from '../utils/draw'

export const useDraw = ({
  settings: initialSettings,
  transparent,
  panZoomDisabled,
  receivedElement,
  scaleReturnedElement,
}: WhiteboardProps) => {
  const [state, dispatch] = useReducer(reducer, {
    elements: [],
    undidElements: [],
    settings: initialSettings,
  })
  const { elements, settings } = state
  const [action, setAction] = useState<Actions | null>()
  const [selectedElement, setSelectedElement] = useState<DrawableElement | null>(null)
  const [panOffset, setPanOffset] = useState<Point>([0, 0])
  const [scale, setScale] = useState<Scale>({ modifier: 1, offset: [0, 0] })
  const [elementToOutput, setElementToOutput] = useState<DrawableElement | null>()
  const [forcedDrawToken, setForcedDrawToken] = useState<number | null>(0)

  const drawingPointsRef = useRef<Point[]>([])
  const textareaRef = useRef<HTMLTextAreaElement>(null)
  const canvasRef = useRef<HTMLCanvasElement | null>(null)
  const selectedVertexRef = useRef<Vertices | null>(null)
  const lastResizeTimeRef = useRef<number | null>(null)

  const computePointInCanvas = useCallback((x: number, y: number, iPanOffset?: Point): Point => {
    if (!canvasRef.current) {
      return [0, 0]
    }

    // Doing opposite of what's happening during context translation
    const fullOffsetX = (iPanOffset || panOffset)[0] * scale.modifier + scale.offset[0]
    const fullOffsetY = (iPanOffset || panOffset)[1] * scale.modifier + scale.offset[1]

    const rect = canvasRef.current.getBoundingClientRect()
    const relativeX = x - rect.left
    const relativeY = y - rect.top

    return [(relativeX - fullOffsetX) / scale.modifier, (relativeY - fullOffsetY) / scale.modifier]
  }, [panOffset, scale])

  const computeAbsolutePoint = useCallback((x: number, y: number): Point => {
    if (!canvasRef.current) {
      return [0, 0]
    }

    const fullOffsetX = panOffset[0] * scale.modifier + scale.offset[0]
    const fullOffsetY = panOffset[1] * scale.modifier + scale.offset[1]

    const rect = canvasRef.current.getBoundingClientRect()
    const absoluteX = x * scale.modifier + fullOffsetX + rect.left
    const absoluteY = y * scale.modifier + fullOffsetY + rect.top

    return [absoluteX, absoluteY]
  }, [panOffset, scale])

  const showTextarea = useCallback((currentPoint: Point, startingPoint: Point) => {
    const textarea = textareaRef.current

    if (!textarea) {
      return
    }

    const absoluteStartingPoint = computeAbsolutePoint(startingPoint[0], startingPoint[1])

    textarea.style.display = 'block'
    textarea.style.fontWeight = settings.bold ? 'bold' : ''
    textarea.style.fontStyle = settings.italic ? 'italic' : ''
    textarea.style.left = `${absoluteStartingPoint[0]}px`
    textarea.style.top = `${absoluteStartingPoint[1]}px`
    textarea.style.width = `${Math.max(300, (currentPoint[0] - startingPoint[0]) * scale.modifier) - 20}px`
    textarea.style.height = `${Math.max(70, (currentPoint[1] - startingPoint[1]) * scale.modifier) - 20}px`
    textarea.style.fontSize = `${settings.fontSize * scale.modifier}px`
  }, [computeAbsolutePoint])

  const handleResize = useCallback(() => {
    lastResizeTimeRef.current = (new Date()).getTime()
    setTimeout(() => {
      const time = (new Date()).getTime()
      if (
        typeof lastResizeTimeRef.current === 'number'
        && time - lastResizeTimeRef.current >= 900
      ) {
        setForcedDrawToken((prevToken) => (prevToken || 0) + 1)
        lastResizeTimeRef.current = null
      }
    }, 1000)
  }, [])

  const init = !canvasRef.current ? (el: HTMLCanvasElement) => {
    if (el) {
      canvasRef.current = el
    }
  } : null

  const redo = useCallback(() => {
    dispatch({ type: ActionTypes.REDO_ELEMENT })
  }, [])

  const undo = useCallback(() => {
    dispatch({ type: ActionTypes.UNDO_ELEMENT })
  }, [])

  const configure = useCallback((changedSettings: Partial<Settings>) => {
    dispatch({ type: ActionTypes.CONFIGURE, payload: changedSettings })
  }, [])

  const onMouseDown: React.MouseEventHandler<HTMLCanvasElement> = useCallback((e) => {
    if (!canvasRef.current) {
      return
    }

    const point = computePointInCanvas(e.clientX, e.clientY)
    drawingPointsRef.current = [point]
    const textarea = textareaRef.current

    const textareaWasOpen = textarea?.style?.display === 'block'

    if (textarea && textareaWasOpen) {
      const textareaRect = textarea.getBoundingClientRect()

      const text = (textarea.value || '').trim()

      if (text) {
        const ctx = canvasRef.current.getContext('2d')
        const element = generateTextElement({
          startingPoint: computePointInCanvas(textareaRect.x, textareaRect.y),
          text,
          settings,
          canvasContext: ctx as CanvasRenderingContext2D,
          textarea,
        })
        dispatch({ type: ActionTypes.ADD_ELEMENT, payload: element })
      }
      textarea.style.display = 'none'
      textarea.style.width = '0px'
      textarea.style.height = '0px'
      textarea.style.resize = 'both'
      textarea.value = ''
      setAction(null)
    }

    switch (settings.cursorMode) {
      case CursorMode.ERASE:
        setAction(Actions.ERASING)
        break
      case CursorMode.PAINT:
        if (settings.drawingForm === DrawingForms.TEXT) {
          if (!textareaWasOpen) {
            setAction(Actions.CREATING_TEXTBOX)
          }
        } else {
          setAction(Actions.DRAWING)
        }
        break
      case CursorMode.SELECTION: {
        if (!point) {
          break
        }

        const element = getElementAtPosition(point[0], point[1], elements || [])

        if (element) {
          const selectedVertex = isOnVertex(point[0], point[1], element)

          if (selectedVertex !== null) {
            selectedVertexRef.current = selectedVertex
            setAction(Actions.RESIZING)
          } else {
            setAction(Actions.MOVING)
          }

          setSelectedElement(element)
        } else {
          setSelectedElement(null)
          setAction(Actions.PANNING)
        }

        break
      }
      default:
        break
    }
  }, [computePointInCanvas, settings, elements])

  const clear = useCallback((removeHistory = true) => {
    const canvas = canvasRef.current

    if (!canvas) {
      return
    }

    const ctx = canvas.getContext('2d')

    if (!ctx) {
      return
    }

    const rect = canvas.getBoundingClientRect()

    // By default filling with white background instead of clearing canvas to avoid video stream mute
    if (!transparent) {
      ctx.save()
      ctx.fillStyle = 'white'
      ctx.fillRect(0, 0, rect.width, rect.height)
      ctx.restore()
      // drawDots(ctx)
    } else {
      ctx.clearRect(0, 0, rect.width, rect.height)
    }

    if (removeHistory) {
      dispatch({ type: ActionTypes.CLEAR_ELEMENTS })
      setScale({ modifier: 1, offset: [0, 0] })
      setPanOffset([0, 0])
      setElementToOutput(null)
    }

  }, [transparent])

  // Clearing old frame and redrawing with new element
  const drawElements = useCallback(() => {
    clear(false)
    const ctx = canvasRef.current?.getContext('2d')

    if (!elements || !ctx) {
      return
    }

    drawElementsOnCanvas(elements, ctx)
  }, [elements, clear])

  useEffect(() => {
    window.addEventListener('resize', handleResize)

    return () => {
      window.removeEventListener('resize', handleResize)
    }
  }, [handleResize])

  useEffect(() => {
    if (typeof forcedDrawToken === 'number') {
      drawElements()
      setForcedDrawToken(null)
    }
  }, [drawElements, forcedDrawToken])

  // To force canvas stream keyframe update so it's not blank black screen on receiver's end
  useEffect(() => {
    const interval = setInterval(() => {
      if (!canvasRef.current) {
        return
      }

      const ctx = canvasRef.current.getContext('2d')
      if (canvasRef.current.width || canvasRef.current.height) {
        ctx!.drawImage(canvasRef.current, 0, 0)
      }
    }, 100)

    return () => {
      clearInterval(interval)
    }
  }, [])

  useEffect(() => {
    if (receivedElement) {
      dispatch({
        type: ActionTypes.ADD_ELEMENT,
        payload: getShiftedElement(receivedElement, panOffset, scale)
      })
    }
  }, [receivedElement, panOffset, scale])

  // Pan and zoom
  useLayoutEffect(() => {
    if (!canvasRef.current) {
      return
    }

    const canvas = canvasRef.current

    const ctx = canvas.getContext('2d')

    if (!ctx) {
      return
    }

    clear(false)
    ctx.save()
    ctx.translate(
      panOffset[0] * scale.modifier + scale.offset[0],
      panOffset[1] * scale.modifier + scale.offset[1]
    )
    ctx.scale(scale.modifier, scale.modifier)
    drawElements()
    ctx.restore()
  }, [panOffset, scale, clear, drawElements])

  useLayoutEffect(() => {
    const mouseMoveHandler = (e: MouseEvent) => {
      const ctx = canvasRef.current?.getContext('2d')

      if (action == null || !ctx || !canvasRef.current) {
        return
      }

      const currentPoint = computePointInCanvas(e.clientX, e.clientY)

      switch (action as Actions) {
        case Actions.DRAWING:
        case Actions.ERASING: {
          if (currentPoint) {
            drawingPointsRef.current.push(currentPoint)
          }

          const elementProps = { drawingPoints: drawingPointsRef.current, settings, shiftKey: e.shiftKey }

          if (action === Actions.ERASING) {
            elementProps.settings = { ...elementProps.settings, drawingForm: DrawingForms.ERASER, strokeColor: '#fff' }
          }

          const newElement = generateShapeElement(elementProps)
          // while mouse keeps moving the last element is essentially a buffer, rewriting it with newly generated elements
          dispatch({ type: ActionTypes.UPDATE_DRAWING_ELEMENT, payload: newElement })

          break
        }
        case Actions.CREATING_TEXTBOX: {
          const textarea = textareaRef.current
          if (!textarea) {
            break
          }

          const startingPoint = drawingPointsRef.current[0]
          showTextarea(currentPoint, startingPoint)

          break
        }
        case Actions.MOVING:
        case Actions.RESIZING:
        case Actions.PANNING:
          {

            const currentPoint = computePointInCanvas(e.clientX, e.clientY, action === Actions.PANNING ? [0, 0] : panOffset)

            const firstPointX = drawingPointsRef.current[0][0]
            const firstPointY = drawingPointsRef.current[0][1]
            const lastPointX = currentPoint ? currentPoint[0] : 0
            const lastPointY = currentPoint ? currentPoint[1] : 0

            const shiftX = lastPointX - firstPointX
            const shiftY = lastPointY - firstPointY

            if (action === Actions.PANNING) {
              setPanOffset([shiftX, shiftY])
              return
            }

            if (!selectedElement) {
              break
            }

            if (currentPoint) {
              drawingPointsRef.current.push(currentPoint)
            }

            let newElement: DrawableElement = {} as DrawableElement
            let drawingPoints: Point[] = [[0, 0]]

            if (
              action === Actions.MOVING &&
              (selectedElement.drawingForm === DrawingForms.PEN || selectedElement.drawingForm === DrawingForms.PENCIL || selectedElement.drawingForm === DrawingForms.BRUSH)
            ) {
              drawingPoints = selectedElement.points.map((point) => [point[0] + shiftX, point[1] + shiftY])
              newElement = generateShapeElement({ id: selectedElement.id, drawingPoints, settings: selectedElement.settings })
            } else if (
              selectedElement.drawingForm === DrawingForms.LINE
              || selectedElement.drawingForm === DrawingForms.RECTANGLE
              || selectedElement.drawingForm === DrawingForms.TEXT
              || selectedElement.drawingForm === DrawingForms.TRIANGLE
              || selectedElement.drawingForm === DrawingForms.CIRCLE
              || selectedElement.drawingForm === DrawingForms.ELLIPSE
            ) {
              if (action === Actions.MOVING) {
                drawingPoints = [
                  [selectedElement.startX + shiftX, selectedElement.startY + shiftY],
                  [selectedElement.endX + shiftX, selectedElement.endY + shiftY],
                ]
              } else if (action === Actions.RESIZING && selectedVertexRef.current !== null
                && (selectedElement.drawingForm === DrawingForms.LINE
                  || selectedElement.drawingForm === DrawingForms.RECTANGLE
                  || selectedElement.drawingForm === DrawingForms.TRIANGLE
                  || selectedElement.drawingForm === DrawingForms.ELLIPSE
                  || selectedElement.drawingForm === DrawingForms.CIRCLE
                )) {
                drawingPoints = resizeCanvasFigure(selectedVertexRef.current, [shiftX, shiftY], selectedElement)
              }

              if (selectedElement.drawingForm === DrawingForms.TEXT) {
                newElement = generateTextElement({
                  id: selectedElement.id,
                  startingPoint: drawingPoints[0],
                  text: selectedElement.text,
                  settings,
                  canvasContext: ctx as CanvasRenderingContext2D,
                })
              } else {
                newElement = generateShapeElement({ id: selectedElement.id, drawingPoints, settings: selectedElement.settings, drawingForm: selectedElement.drawingForm })
              }
            }

            dispatch({ type: ActionTypes.UPDATE_ELEMENT, payload: { id: selectedElement.id, element: newElement } })

            break
          }
        default:
          break
      }
    }

    const mouseWheelHandler = (e: WheelEvent) => {
      if (panZoomDisabled) {
        return
      }

      e.preventDefault()
      if (e.shiftKey) {
        setPanOffset((prevOffset) => ([prevOffset[0] + e.deltaY * 0.1, prevOffset[1]]))
      } else if (e.altKey) {
        setPanOffset((prevOffset) => ([prevOffset[0], prevOffset[1] + e.deltaY * 0.1]))
      } else if ((e.ctrlKey || e.metaKey) && canvasRef.current) {
        setScale((prevScale) => {
          const mousePosition = computePointInCanvas(e.clientX, e.clientY)
          const modifier = Math.min(Math.max(prevScale.modifier - e.deltaY * 0.0001, 0.01), 2)
          const modifierDelta = prevScale.modifier - modifier

          const offsetX = prevScale.offset[0] + mousePosition[0] * modifierDelta
          const offsetY = prevScale.offset[1] + mousePosition[1] * modifierDelta

          return {
            modifier,
            offset: [offsetX, offsetY],
          }
        })
      }
    }

    const mouseUpHandler = (e: MouseEvent) => {
      // Not updating elements after creating textbox as user still has to type in
      if (action !== Actions.CREATING_TEXTBOX) {
        // Using a timeout to defer element finalization after action is null, so that weird duplicate dispatch doesn't happen possibly because of mousemove
        setTimeout(
          () => dispatch({ type: ActionTypes.FINALIZE_DRAWING_ELEMENT })
        )
      } else {
        const textarea = textareaRef.current
        const currentPoint = computePointInCanvas(e.clientX, e.clientY)

        if (!textarea) {
          return
        }

        if (textarea.style.display !== 'block') {
          const startingPoint = drawingPointsRef.current[0]
          showTextarea(currentPoint, startingPoint)
        }
      }

      setAction(null)
    }

    const textareaKeyUpHandler = () => {
      const textarea = textareaRef.current
      if (textarea) {
        textarea.style.height = `${textarea.scrollHeight}px`
      }
    }

    const dblClickHandler = () => {
      if (selectedElement && selectedElement.drawingForm === DrawingForms.TEXT) {
        const textarea = textareaRef.current
        const startingPoint: Point = [selectedElement.startX, selectedElement.startY]
        const currentPoint: Point = [selectedElement.endX, selectedElement.endY]

        dispatch({ type: ActionTypes.DELETE_ELEMENT, payload: selectedElement.id })

        showTextarea(currentPoint, startingPoint)
        textarea!.value = selectedElement.text
        textarea!.style.height = 'auto'
        textarea!.style.width = 'auto'
        textareaKeyUpHandler()
      }
    }

    canvasRef.current?.addEventListener('mousemove', mouseMoveHandler)
    canvasRef.current?.addEventListener('wheel', mouseWheelHandler)
    canvasRef.current?.addEventListener('dblclick', dblClickHandler)
    window.addEventListener('mouseup', mouseUpHandler)

    const undoRedoHandler = getUndoRedoHandler(undo, redo)
    document.addEventListener('keydown', undoRedoHandler)

    document.addEventListener('keyup', textareaKeyUpHandler)

    // eslint-disable-next-line react-hooks/exhaustive-deps
    return () => {
      // eslint-disable-next-line react-hooks/exhaustive-deps
      canvasRef.current?.removeEventListener('mousemove', mouseMoveHandler)
      canvasRef.current?.removeEventListener('wheel', mouseWheelHandler)
      canvasRef.current?.removeEventListener('dblclick', dblClickHandler)
      window.removeEventListener('mouseup', mouseUpHandler)
      document.removeEventListener('keydown', undoRedoHandler)
      document.removeEventListener('keyup', textareaKeyUpHandler)
    }
  }, [action, settings, undo, redo, selectedElement, panOffset, computePointInCanvas, computeAbsolutePoint, showTextarea, scale.modifier, panZoomDisabled])

  useEffect(() => {
    const lastElement = elements.filter(({ beingDrawn }) => !beingDrawn).slice(-1)[0]
    const ctx = canvasRef.current?.getContext('2d')
    if (lastElement && lastElement.id !== elementToOutput?.id && scaleReturnedElement && ctx) {
      const element = getScaledElement(scaleReturnedElement, lastElement, ctx)
      setElementToOutput(element)
    }
  }, [elements, scaleReturnedElement, elementToOutput])

  return {
    init,
    onMouseDown,
    clear,
    settings,
    configure,
    undo,
    redo,
    action,
    textareaRef,
    scale,
    setScale,
    newElement: elementToOutput,
    canvas: canvasRef?.current,
  }
}
