import { LegacyRef, useCallback, useEffect, useRef, useState } from 'react'

import WhiteboardControls from '../components/WhiteboardControls'
import { DEFAULT_WHITEBOARD_HEIGHT, DEFAULT_WHITEBOARD_WIDTH, defaultSettings, WHITEBOARD_CONTROL_BAR_HEIGHT } from '../constants'
import { useDraw } from '../hooks/useDraw'
import { DrawableElement, DrawingForms } from '../types/typing'
import { getVideoDimensions } from '../utils/getVideoDimensions'
type Props = {
  onElementAdd: (element: DrawableElement | null) => void
  videoElement: HTMLVideoElement | undefined // Respective canvas stream element, used for sizing purposes
  darkMode?: boolean
}

// Used for drawing on a whiteboard video on a stream receiving participant
const WhiteboardStreamController: React.FC<Props> = ({
  onElementAdd,
  videoElement,
  darkMode,
}) => {
  const [whiteboardScale, setWhiteboardScale] = useState<number>()
  const {
    init,
    onMouseDown,
    clear,
    settings,
    configure,
    newElement,
    textareaRef,
  } = useDraw({ settings: defaultSettings, transparent: true, panZoomDisabled: true, scaleReturnedElement: whiteboardScale })
  const [canvas, setCanvas] = useState<HTMLCanvasElement>()
  const [videoDimensions, setVideoDimensions] = useState({ width: 0, height: 0 })

  const setVideoDimensionsSafe = useCallback((dimensions: typeof videoDimensions) => {
    if (dimensions.width || dimensions.height) {
      setVideoDimensions(dimensions)
    }
  }, [])

  const wrapperRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!canvas || !init) {
      return
    }

    init(canvas)
  }, [canvas, init])

  useEffect(() => {
    canvas?.addEventListener('mousedown', onMouseDown as unknown as EventListener)

    return () => {
      canvas?.removeEventListener('mousedown', onMouseDown as unknown as EventListener)
    }
  }, [canvas, onMouseDown])

  useEffect(() => {
    if (newElement) {
      onElementAdd(newElement)
    }
    setTimeout(() => {
      // Average time it would take to send element to the whiteboard host and show the updated stream
      clear()
    }, 2000)
  }, [newElement, onElementAdd, clear])

  useEffect(() => {
    if (videoElement) {
      let resizeObserver: ResizeObserver
      let timeoutToken: number

      if (!canvas) {
        return
      }

      const onResize = () => {
        const videoDimensions = getVideoDimensions(videoElement)

        if (wrapperRef.current) {
          const wrapperDimensions = wrapperRef.current.getBoundingClientRect()

          const scales = {
            width: wrapperDimensions.width / videoDimensions.width,
            height: (wrapperDimensions.height - WHITEBOARD_CONTROL_BAR_HEIGHT) / videoDimensions.height
          }
          const diffs = {
            width: wrapperDimensions.width - videoDimensions.width,
            height: (wrapperDimensions.height - WHITEBOARD_CONTROL_BAR_HEIGHT) - videoDimensions.height
          }

          let width = videoDimensions.width
          let height = videoDimensions.height

          // Whiteboard is overflowing from its container div
          if (scales.width < 1 || scales.height < 1) {
            if (scales.width < 1 && scales.height < 1) {
              // Which whiteboard dimension is less fitting. Resizing whiteboard according to it
              const dimension = scales.width < scales.height ? 'width' : 'height'
              const scale: number = scales[dimension]
              width = videoDimensions.width * scale
              height = videoDimensions.height * scale
              setVideoDimensionsSafe({ width, height })
            } else if (scales.width < 1) {
              width = videoDimensions.width * scales.width
              height = videoDimensions.height * scales.width
              setVideoDimensionsSafe({ width, height })
            } else if (scales.height < 1) {
              width = videoDimensions.width * scales.height
              height = videoDimensions.height * scales.height
              setVideoDimensionsSafe({ width, height })
            }
          }
          // Whiteboard wrapper div is larger or fitting default whiteboard size
          else if (wrapperDimensions.width >= DEFAULT_WHITEBOARD_WIDTH
            && (wrapperDimensions.height - WHITEBOARD_CONTROL_BAR_HEIGHT) >= DEFAULT_WHITEBOARD_HEIGHT
            && !(canvas.width === DEFAULT_WHITEBOARD_WIDTH && canvas.height === DEFAULT_WHITEBOARD_HEIGHT)
          ) {
            width = DEFAULT_WHITEBOARD_WIDTH
            height = DEFAULT_WHITEBOARD_HEIGHT
            setVideoDimensionsSafe({ width, height })
          }
          // Whiteboard is significantly smaller than its container
          else if ((Math.abs(diffs.width) >= 3 && videoDimensions.width - canvas.height <= 1)
            || (Math.abs(diffs.height) >= 3 && videoDimensions.height - canvas.height <= 1)
          ) {
            const dimension = scales.width < scales.height ? 'width' : 'height'
            const scale: number = scales[dimension]
            width = videoDimensions.width * scale
            height = videoDimensions.height * scale
            setVideoDimensionsSafe({ width, height })
          }

          // In any case, update scale number used to reposition received elements
          setWhiteboardScale(DEFAULT_WHITEBOARD_WIDTH / width)

          const videoElementDimensions = videoElement.getBoundingClientRect()

          // Centering canvas and video element inside controller div by keeping controller bar height in mind
          canvas.style.top = `${(wrapperDimensions.height - videoElementDimensions.height) / 2}px`

          videoElement.style.scale = String(width / videoDimensions.width)
          videoElement.style.position = 'relative'
          videoElement.style.top = `${(wrapperDimensions.height - videoElementDimensions.height) / 2}px`
        }
      }

      resizeObserver = new ResizeObserver(onResize)
      resizeObserver.observe(videoElement)

      if (!(videoDimensions.width || videoDimensions.height)) {
        onResize()
      }

      // As the video dimensions usually keeps changing because of bandwidth, etc.
      // TODO: refactor this to find a better more consistent way
      timeoutToken = setInterval(onResize, 1000)

      return () => {
        resizeObserver.disconnect()
        clearTimeout(timeoutToken)
      }
    }
  }, [videoElement, videoDimensions, canvas, setVideoDimensionsSafe])

  return (
    <div className="absolute top-0 flex items-center justify-center w-full h-full" ref={wrapperRef}>
      <canvas
        style={{ width: `${videoDimensions.width}px`, height: `${videoDimensions.height}px` }}
        className="relative"
        width={videoDimensions.width}
        height={videoDimensions.height}
        ref={setCanvas as LegacyRef<HTMLCanvasElement>}
      />
      <WhiteboardControls
        configure={configure}
        settings={settings}
        darkMode={darkMode}
        hostMode={false}
      />
      {settings.drawingForm === DrawingForms.TEXT
        && <textarea
          ref={textareaRef}
          className="hidden fixed bg-gray-50 focus:bg-gray-100"
          id="text-input"
          style={{
            fontSize: `${settings.fontSize}px`,
            fontFamily: settings.fontFamily,
            lineHeight: `${settings.lineHeight}px`,
            color: settings.strokeColor,
          }}
        />
      }
    </div>
  )
}

export default WhiteboardStreamController
