import { v4 as uuidv4 } from 'uuid'

import { DrawParams, DrawableElement, DrawingForms, Point, Scale, WriteParams } from '../types/typing'

export function generateTextElement({ id, startingPoint, text: _text, settings, canvasContext: ctx, textarea }: WriteParams): DrawableElement {
  ctx.save()
  ctx.font = `${settings.fontSize}px ${settings.fontFamily}`
  ctx.textBaseline = 'top'

  let text = _text
  let subStr = ''
  let lastLineBreakIndex = 0
  let preText = ''

  if (textarea) {
    for (let index = 1; index <= text.length; index++) {
      subStr = text.slice(lastLineBreakIndex, index)
      const { width } = ctx.measureText(subStr)

      if (width >= textarea.clientWidth) {
        text = preText.concat(subStr.concat('\n').concat(text.slice(index)))
        index++
        lastLineBreakIndex = index
        preText = text.slice(0, lastLineBreakIndex)
      }
    }
  }

  const { width } = ctx.measureText(text)
  const height = text.split('\n').length * settings.lineHeight
  ctx.restore()

  const endX = startingPoint[0] + width
  const endY = startingPoint[1] + height

  return {
    drawingForm: DrawingForms.TEXT,
    id: id || uuidv4(),
    startX: startingPoint[0],
    startY: startingPoint[1],
    endX,
    endY,
    settings,
    text,
    // text is rendered on canvas only after textarea submit, not while typing
    beingDrawn: false,
  }
}

export function generateShapeElement({ id, drawingPoints, settings, drawingForm: _drawingForm, shiftKey = false, beingDrawn = true }: DrawParams): DrawableElement {
  const startPoint = drawingPoints[0]
  const currentPoint = drawingPoints[drawingPoints.length - 1]
  const [currX, currY] = currentPoint

  let endX = currX
  let endY = currY

  let element = {}
  let drawingForm = _drawingForm || settings.drawingForm

  switch (settings.drawingForm) {
    case DrawingForms.ELLIPSE: {
      if (shiftKey) {
        drawingForm = DrawingForms.CIRCLE
      }
      break
    }
    case DrawingForms.TRIANGLE: {
      // Swap triangle bottom vertices if needed to reflect order from left to right
      endX = Math.max(startPoint[0] - (endX - startPoint[0]), endX)
      break
    }
    case DrawingForms.PENCIL:
    case DrawingForms.PEN:
    case DrawingForms.ERASER:
    case DrawingForms.BRUSH:
      {
        element = {
          points: drawingPoints,
        }

        break
      }
    default:
      break
  }

  return {
    ...element,
    settings,
    id: id || null,
    drawingForm,
    beingDrawn,
    startX: startPoint[0],
    startY: startPoint[1],
    endX,
    endY,
  } as DrawableElement
}

const shiftPoint = (point: Point, panOffset: Point, scale: Scale): Point => [
  (point[0] - (panOffset[0] * scale.modifier + scale.offset[0])) / scale.modifier,
  (point[1] - (panOffset[1] * scale.modifier + scale.offset[1])) / scale.modifier
]

export const getShiftedElement = (element: DrawableElement, panOffset: Point, scale: Scale) => {
  let shiftedElement = { ...element }

  switch (shiftedElement.drawingForm) {
    case DrawingForms.PENCIL:
    case DrawingForms.PEN:
    case DrawingForms.ERASER:
    case DrawingForms.BRUSH:
      {
        shiftedElement.points = shiftedElement.points.map((point) => shiftPoint(point, panOffset, scale))

        break
      }
    default: {
      const shiftedStart = shiftPoint([shiftedElement.startX, shiftedElement.startY], panOffset, scale)
      const shiftedEnd = shiftPoint([shiftedElement.endX, shiftedElement.endY], panOffset, scale)

      shiftedElement.startX = shiftedStart[0]
      shiftedElement.startY = shiftedStart[1]
      shiftedElement.endX = shiftedEnd[0]
      shiftedElement.endY = shiftedEnd[1]

      break
    }
  }

  return shiftedElement
}

export const finalizeElement = (element: DrawableElement): DrawableElement => {
  return {
    ...element,
    id: element.id || uuidv4(),
    beingDrawn: false,
  }
}

export const getScaledElement = (scale: number, element: DrawableElement, canvasContext: CanvasRenderingContext2D) => {
  switch (element.drawingForm) {
    case DrawingForms.CIRCLE:
    case DrawingForms.ELLIPSE:
    case DrawingForms.LINE:
    case DrawingForms.RECTANGLE:
    case DrawingForms.TRIANGLE: {
      return generateShapeElement({
        id: element.id,
        settings: element.settings,
        drawingPoints: [
          [element.startX * scale, element.startY * scale],
          [element.endX * scale, element.endY * scale],
        ],
        beingDrawn: element.beingDrawn,
      })
    }
    case DrawingForms.PEN:
    case DrawingForms.PENCIL:
    case DrawingForms.BRUSH:
    case DrawingForms.ERASER:
      {
        return generateShapeElement({
          id: element.id,
          settings: element.settings,
          drawingPoints: element.points.map(([x, y]) => ([x * scale, y * scale])),
          beingDrawn: element.beingDrawn,
        })
      }
    case DrawingForms.TEXT:
      return generateTextElement({
        id: element.id,
        startingPoint: [element.startX * scale, element.startY * scale],
        text: element.text,
        settings: element.settings,
        canvasContext,
      })
  }
}
