export const getUndoRedoHandler = (undo: () => void, redo: () => void) => (e: KeyboardEvent) => {
  if (e.ctrlKey) {
    switch (e.code) {
      case 'KeyZ':
        if (e.shiftKey) {
          redo()
        } else {
          undo()
        }
        break
      case 'KeyY':
        redo()
        break
      default:
        break
    }
  }
}
