import { DrawableElement, DrawingForms, Point, Vertices } from '../types/typing'
import { distance, isInsideEllipse, isOnEllipse, isOnLine } from './geometry'

const ALLOWED_OFFSET = 20

const isNearVertex = (a: Point, b: Point, allowedOffset: number): boolean =>
  Math.abs(distance(a, b)) < allowedOffset

const isWithinElement = (x: number, y: number, element: DrawableElement) => {
  const { drawingForm, settings: { strokeWidth } } = element

  if (drawingForm === DrawingForms.RECTANGLE
    || drawingForm === DrawingForms.LINE
    || drawingForm === DrawingForms.TEXT
    || drawingForm === DrawingForms.TRIANGLE
    || drawingForm === DrawingForms.ELLIPSE
    || drawingForm === DrawingForms.CIRCLE
  ) {
    const { startX, endX, startY, endY } = element

    if (drawingForm === DrawingForms.RECTANGLE || drawingForm === DrawingForms.TEXT) {
      const minX = Math.min(startX, endX) - strokeWidth / 2 // Naturally stroke width halves are added on left and right sides of the point
      const maxX = Math.max(startX, endX) + strokeWidth / 2
      const minY = Math.min(startY, endY) - strokeWidth / 2
      const maxY = Math.max(startY, endY) + strokeWidth / 2

      return x >= minX && x <= maxX && y >= minY && y <= maxY
    } else if (drawingForm === DrawingForms.TRIANGLE) {
      const allowedOffset = strokeWidth * 1.5 // Used for border detection improvement

      // Left bottom vertex used as a virtual mathematical coordinate system zero point
      const topX = endX - startX
      const topY = Math.abs(endY - startY) + allowedOffset // Y might be inverted towards the horizontal axis
      const rightX = topX * 2 + allowedOffset

      const needsNormalization = startY > endY
      const normalizedY = needsNormalization ? y - 2 * (y - endY) : y // Mirror the point towards the triangle flat horizontal side

      const leftXAbsolute = startX - (endX - startX) - allowedOffset

      const distanceToBottom = (endY + strokeWidth / 2) - normalizedY
      const distanceToBottomLeft = x - leftXAbsolute
      const distanceToBottomRight = (endX + allowedOffset) - x

      const bottomAngle = Math.atan(topY / (rightX / 2)) // Bottom left and right angles are equal
      const angleToBottomLeft = Math.atan(distanceToBottom / distanceToBottomLeft)
      const angleToBottomRight = Math.atan(distanceToBottom / distanceToBottomRight)

      // A point is inside a triangle if it's resting between left and right bottom angles
      return 0 <= angleToBottomLeft && angleToBottomLeft <= bottomAngle && 0 <= angleToBottomRight && angleToBottomRight <= bottomAngle
    } else if (drawingForm === DrawingForms.ELLIPSE || drawingForm === DrawingForms.CIRCLE) {
      return isInsideEllipse([x, y], element)
    }
    else {
      const a: Point = [startX, startY]
      const b: Point = [endX, endY]
      const c: Point = [x, y]

      return isOnLine(a, b, c, 1)
    }
  } else if (drawingForm === DrawingForms.PEN || drawingForm === DrawingForms.PENCIL || drawingForm === DrawingForms.BRUSH) {
    const betweenAnyPoint = element.points.some((point, index) => {
      const nextPoint = element.points[index + 1]
      if (!nextPoint) {
        return false
      }
      return isOnLine(point as Point, nextPoint as Point, [x, y], ALLOWED_OFFSET + strokeWidth)
    })

    return !!betweenAnyPoint
  }
}

export const getElementAtPosition = (x: number, y: number, elements: DrawableElement[]) => {
  return elements.find(element => isWithinElement(x, y, element))
}

export const isOnVertex = (x: number, y: number, element: DrawableElement, allowedOffset: number = ALLOWED_OFFSET): Vertices | null => {
  const { settings: { strokeWidth } } = element
  let endX = 0, endY = 0,
    rightBottom: Point = [0, 0],
    leftTop: Point = [0, 0],
    startX = 0, startY = 0

  const point: Point = [x, y]

  switch (element.drawingForm) {
    case DrawingForms.LINE:
    case DrawingForms.RECTANGLE:
    case DrawingForms.TRIANGLE:
    case DrawingForms.ELLIPSE:
    case DrawingForms.CIRCLE:
      {
        startX = element.startX
        startY = element.startY
        leftTop = [startX, startY]
        endX = element.endX
        endY = element.endY
        rightBottom = [endX, endY]
        break
      }
    default:
      break
  }

  switch (element.drawingForm) {
    case DrawingForms.LINE: {
      if (isNearVertex(point, leftTop, allowedOffset + strokeWidth)) {
        return Vertices.TOP_LEFT
      }
      if (isNearVertex(point, rightBottom, allowedOffset + strokeWidth)) {
        return Vertices.BOTTOM_RIGHT
      }
      return null
    }
    case DrawingForms.TRIANGLE: {
      const leftBottom: Point = [startX - (endX - startX), endY]

      if (isNearVertex(point, leftTop, allowedOffset + strokeWidth)) {
        return Vertices.TOP_LEFT
      }
      if (isNearVertex(point, leftBottom, allowedOffset + strokeWidth)) {
        return Vertices.BOTTOM_LEFT
      }
      if (isNearVertex(point, rightBottom, allowedOffset + strokeWidth)) {
        return Vertices.BOTTOM_RIGHT
      }
      return null
    }
    case DrawingForms.RECTANGLE: {
      const rightTop: Point = [endX, startY]
      const leftBottom: Point = [startX, endY]

      if (isNearVertex(point, leftTop, allowedOffset + strokeWidth)) {
        return Vertices.TOP_LEFT
      }
      if (isNearVertex(point, rightTop, allowedOffset + strokeWidth)) {
        return Vertices.TOP_RIGHT
      }
      if (isNearVertex(point, leftBottom, allowedOffset + strokeWidth)) {
        return Vertices.BOTTOM_LEFT
      }
      if (isNearVertex(point, rightBottom, allowedOffset + strokeWidth)) {
        return Vertices.BOTTOM_RIGHT
      }
      return null
    }
    case DrawingForms.ELLIPSE:
    case DrawingForms.CIRCLE: {
      if (isOnEllipse([x, y], element)) {
        return Vertices.BOTTOM_RIGHT
      }
      return null
    }
    default:
      return null
  }
}