import { Reducer } from 'react'

import { DrawableElement, Settings } from '../types/typing'
import { finalizeElement } from '../utils/generateElement'

export type State = {
  elements: DrawableElement[]
  undidElements: DrawableElement[]
  settings: Settings
}

export enum ActionTypes {
  ADD_ELEMENT,
  UNDO_ELEMENT,
  REDO_ELEMENT,
  CLEAR_ELEMENTS,
  DELETE_ELEMENT,
  UPDATE_DRAWING_ELEMENT,
  FINALIZE_DRAWING_ELEMENT,
  UPDATE_ELEMENT,
  CONFIGURE,
}

type AddAction = {
  type: ActionTypes.ADD_ELEMENT
  payload: DrawableElement
}

type UndoAction = {
  type: ActionTypes.UNDO_ELEMENT
}

type RedoAction = {
  type: ActionTypes.REDO_ELEMENT
}

type ClearElementsAction = {
  type: ActionTypes.CLEAR_ELEMENTS
}

type DeleteElementAction = {
  type: ActionTypes.DELETE_ELEMENT,
  payload: DrawableElement['id']
}

type UpdateDrawingElementAction = {
  type: ActionTypes.UPDATE_DRAWING_ELEMENT
  payload: DrawableElement
}

type FinalizeDrawingElementAction = {
  type: ActionTypes.FINALIZE_DRAWING_ELEMENT
}

type UpdateElementAction = {
  type: ActionTypes.UPDATE_ELEMENT
  payload: {
    id: string | null
    element: DrawableElement
  }
}

type ConfigureAction = {
  type: ActionTypes.CONFIGURE
  payload: Partial<Settings>
}

type Actions = AddAction | UndoAction | RedoAction | ClearElementsAction | DeleteElementAction | UpdateDrawingElementAction | FinalizeDrawingElementAction | UpdateElementAction | ConfigureAction

const reducer: Reducer<State, Actions> = (state, action) => {
  const lastElement = state.elements[state.elements.length - 1]

  switch (action.type) {
    case ActionTypes.ADD_ELEMENT: {
      return {
        ...state,
        elements: !state.elements.find(({ id }) => action.payload.id === id) ? [...state.elements, action.payload] : state.elements,
      }
    }
    case ActionTypes.UNDO_ELEMENT: {
      return {
        ...state,
        elements: state.elements.slice(0, state.elements.length - 1),
        undidElements: lastElement ? [...state.undidElements, lastElement] : state.undidElements,
      }
    }
    case ActionTypes.REDO_ELEMENT: {
      const lastUndidElement = state.undidElements[state.undidElements.length - 1]

      if (!lastUndidElement) {
        return state
      }

      return {
        ...state,
        elements: [...state.elements, lastUndidElement],
        undidElements: state.undidElements.slice(0, state.undidElements.length - 1),
      }
    }
    case ActionTypes.CLEAR_ELEMENTS: {
      return {
        ...state,
        elements: [],
      }
    }
    case ActionTypes.DELETE_ELEMENT: {
      if (!action.payload) {
        return state
      }

      return {
        ...state,
        elements: state.elements.filter(({ id }) => id !== action.payload),
      }
    }
    case ActionTypes.UPDATE_DRAWING_ELEMENT: {
      return {
        ...state,
        elements: [
          ...state.elements.slice(0, state.elements.length - (lastElement?.beingDrawn ? 1 : 0)),
          action.payload,
        ],
        undidElements: [],
      }
    }
    case ActionTypes.FINALIZE_DRAWING_ELEMENT: {
      return {
        ...state,
        elements: state.elements.length
          ? [
            ...(state.elements.slice(0, state.elements.length - 1)),
            finalizeElement(lastElement),
          ]
          : state.elements,
      }
    }
    case ActionTypes.UPDATE_ELEMENT: {
      const index = state.elements.findIndex(({ id }) => id === action.payload.id)

      if (index === -1) {
        return state
      }

      return {
        ...state,
        elements: [
          ...state.elements.slice(0, index),
          action.payload.element,
          ...state.elements.slice(index + 1, state.elements.length),
        ],
        undidElements: [],
      }
    }
    case ActionTypes.CONFIGURE: {
      return {
        ...state,
        settings: {
          ...state.settings,
          ...action.payload,
        }
      }
    }
    default:
      return state
  }
}

export default reducer
