import React, { useCallback, useEffect, useRef, useState } from 'react'

import { Actions, ControlTools, CursorMode, DrawingForms, Fonts, Settings } from '../types/typing'
import './WhiteboardControls.scss'

// import AlignLogo from './icons/align.svg?react'
import BoldLogo from './icons/b.svg?react'
import CircleLogo from './icons/circle.svg?react'
import EraserLogo from './icons/eraser.svg?react'
import ItalicLogo from './icons/i.svg?react'
// import InsertLinkLogo from './icons/insert-link.svg?react'
// import LinkLogo from './icons/link.svg?react'
import MouseLogo from './icons/mouse.svg?react'
import Pen2Logo from './icons/pen-2.svg?react'
import Pen3Logo from './icons/pen-3.svg?react'
import PenLogo from './icons/pen.svg?react'
import PencilLogo from './icons/pencil.svg?react'
import RedoLogo from './icons/redo.svg?react'
// import SelectLogo from './icons/select.svg?react'
import { COLORS } from '../constants'
import ShapeLogo from './icons/shape.svg?react'
import sliderImgUrl from './icons/slider-bg.png'
import SquareLogo from './icons/square.svg?react'
import TextLogo from './icons/text.svg?react'
import TriangleLogo from './icons/triangle.svg?react'
import UndoLogo from './icons/undo.svg?react'
import DropdownLogo from './icons/wb-dropdown.svg?react'

const StrokeWidthRange: React.FC<React.DetailedHTMLProps<React.InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>> = React.memo(({ defaultValue, onChange }) => {
  const [el, setEl] = useState<HTMLInputElement | null>(null)
  const isMouseHoldingRef = useRef(false)
  const debounceCounterRef = useRef(0)

  useEffect(() => {
    if (!el) {
      return
    }

    el.style.backgroundImage = `url("${sliderImgUrl}")`

    const mouseDownHandler = () => {
      isMouseHoldingRef.current = true
    }
    const mouseUpHandler = () => {
      isMouseHoldingRef.current = false
    }
    const mouseMoveHandler = (e: MouseEvent) => {
      debounceCounterRef.current++

      if (debounceCounterRef.current % 4 || !isMouseHoldingRef.current) {
        debounceCounterRef.current = 0
        return
      }

      const { width } = el.getBoundingClientRect()
      const value = (e.movementX / width) * (Number(el.max) - Number(el.min))
      if (onChange) {
        onChange({ target: { value } } as unknown as React.ChangeEvent<HTMLInputElement>)
      }
    }

    el.addEventListener('mousedown', mouseDownHandler)
    window.addEventListener('mouseup', mouseUpHandler)
    el.addEventListener('mousemove', mouseMoveHandler)

    return () => {
      el.removeEventListener('mousedown', mouseDownHandler)
      window.removeEventListener('mouseup', mouseUpHandler)
      el.removeEventListener('mousemove', mouseMoveHandler)
    }
  }, [el])

  return <input
    type="range"
    min="5"
    max="25"
    defaultValue={defaultValue}
    onChange={onChange}
    ref={setEl}
    className="wb-range"
    id="wb-range"
  />
})

export type Props = {
  configure: (changedSettings: Partial<Settings>) => void
  settings: Settings
  onZoomOut?: () => void
  onZoomIn?: () => void
  onClear?: () => void
  undo?: () => void
  redo?: () => void
  closeWhiteboard?: () => void
  darkMode?: boolean
  hostMode?: boolean
  transparent?: boolean
  action?: Actions | null
}

const WhiteboardControls: React.FC<Props> = ({
  settings,
  configure,
  undo,
  redo,
  closeWhiteboard,
  darkMode,
  hostMode,
  transparent,
  action,
}) => {
  const [selectedTool, setSelectedTool] = useState<ControlTools>(ControlTools.PENCIL)

  const onColorChange = useCallback((color: string) => {
    configure({ strokeColor: color })
  }, [configure])

  const onMouseLogoClick = () => {
    configure({ cursorMode: CursorMode.SELECTION })
    setSelectedTool(ControlTools.MOUSE)
  }

  const onShapeLogoClick = () => {
    configure({ cursorMode: CursorMode.PAINT, drawingForm: DrawingForms.TRIANGLE })
    setSelectedTool(ControlTools.SHAPE)
  }

  const onEraserLogoClick = () => {
    configure({ cursorMode: CursorMode.ERASE })
    setSelectedTool(ControlTools.ERASER)
  }

  const onTextLogoClick = () => {
    configure({ cursorMode: CursorMode.PAINT, drawingForm: DrawingForms.TEXT })
    setSelectedTool(ControlTools.TEXT)
  }

  const onPencilLogoClick = () => {
    configure({ cursorMode: CursorMode.PAINT, drawingForm: DrawingForms.PEN })
    setSelectedTool(ControlTools.PENCIL)
  }

  const onSelectFontSize = (fontSize: number) => {
    configure({ fontSize })
  }


  const onWidthChange: React.ChangeEventHandler<HTMLInputElement> = useCallback((e) => {
    configure({ strokeWidth: Number(e.target.value) })
  }, [configure])

  const ColorButton = useCallback(({ color }: { color: string }) =>
    <div className={`wb-color ${settings.strokeColor === color ? 'active' : ''}`}>
      <button
        style={{ background: color }}
        onClick={() => onColorChange(color)}
      />
    </div>, [settings.strokeColor, onColorChange])

  const ColorPicker = useCallback(() => (
    <div className="wb-color-picker">
      {COLORS.map((color, index) => <ColorButton key={`${color}-${index}`} color={color} />)}
    </div>
  ), [ColorButton])

  return (
    <div className={`whiteboard-tools-container ${darkMode ? 'dark' : ''}`}>
      <div className={`whiteboard-control ${transparent ? 'transparent hover:pointer-events-none' : ''} ${transparent && action == null ? 'hover:pointer-events-auto' : ''}`}>
        <div className="whiteboard-tools-options">
          {
            selectedTool === ControlTools.TEXT && (
              <div className="wb-font-options">
                <div className="wb-tools-wrapper">
                  <button className="wb-choose"><span>{settings.fontFamily}</span><DropdownLogo /></button>
                  <div className="wb-additional-wrapper">
                    <div className="wb-additional">
                      <button
                        className={`${settings.fontFamily === Fonts.ARIAL ? 'active' : ''} wb-list`}
                        onClick={() => configure({ fontFamily: Fonts.ARIAL })}
                      >
                        {Fonts.ARIAL}
                      </button>
                      <button
                        className={`${settings.fontFamily === Fonts.TIMES_NEW_ROMAN ? 'active' : ''} wb-list`}
                        onClick={() => configure({ fontFamily: Fonts.TIMES_NEW_ROMAN })}
                      >
                        {Fonts.TIMES_NEW_ROMAN
                        }
                      </button>
                      <button
                        className={`${settings.fontFamily === Fonts.VERDANA ? 'active' : ''} wb-list`}
                        onClick={() => configure({ fontFamily: Fonts.VERDANA })}
                      >
                        {Fonts.VERDANA}
                      </button>
                      <button
                        className={`${settings.fontFamily === Fonts.COURIER_NEW ? 'active' : ''} wb-list`}
                        onClick={() => configure({ fontFamily: Fonts.COURIER_NEW })}
                      >
                        {Fonts.COURIER_NEW}
                      </button>
                    </div>
                  </div>
                </div>
                <div className="wb-tools-wrapper">
                  <button className="wb-choose"><span>{settings.fontSize}</span><DropdownLogo /></button>
                  <div className="wb-additional-wrapper">
                    <div className="wb-additional">
                      <button className={`wb-list ${settings.fontSize == 14 ? 'active' : ''}`} onClick={() => onSelectFontSize(14)}>14</button>
                      <button className={`wb-list ${settings.fontSize == 16 ? 'active' : ''}`} onClick={() => onSelectFontSize(16)}>16</button>
                      <button className={`wb-list ${settings.fontSize == 18 ? 'active' : ''}`} onClick={() => onSelectFontSize(18)}>18</button>
                      <button className={`wb-list ${settings.fontSize == 24 ? 'active' : ''}`} onClick={() => onSelectFontSize(24)}>24</button>
                      <button className={`wb-list ${settings.fontSize == 30 ? 'active' : ''}`} onClick={() => onSelectFontSize(30)}>30</button>
                    </div>
                  </div>
                </div>
              </div>
            )
          }
          {
            selectedTool === ControlTools.SHAPE && (
              <>
                <button
                  className={`wb triangle tip-bottom ${settings.drawingForm === DrawingForms.TRIANGLE ? 'active' : ''}`}
                  data-title="Triangle"
                  onClick={() => configure({ drawingForm: DrawingForms.TRIANGLE })}
                >
                  <TriangleLogo />
                </button>
                <button
                  className={`wb circle tip-bottom ${settings.drawingForm === DrawingForms.ELLIPSE ? 'active' : ''}`}
                  data-title="Circle"
                  onClick={() => configure({ drawingForm: DrawingForms.ELLIPSE })}
                >
                  <CircleLogo />
                </button>
                <button
                  className={`wb square tip-bottom ${settings.drawingForm === DrawingForms.RECTANGLE ? 'active' : ''}`}
                  data-title="Square"
                  onClick={() => configure({ drawingForm: DrawingForms.RECTANGLE })}
                >
                  <SquareLogo />
                </button>
              </>
            )
          }
          {
            selectedTool === ControlTools.PENCIL && (
              <>
                <div className="wb-tools-wrapper">
                  <button
                    className={`wb pen tip-bottom ${settings.drawingForm === DrawingForms.PEN ? 'active' : ''}`}
                    data-title="Pen"
                    onClick={() => configure({ drawingForm: DrawingForms.PEN })}
                  >
                    <PenLogo />
                  </button>
                </div>
                <div className="wb-tools-wrapper">
                  <button
                    className={`wb pen-2 tip-bottom ${settings.drawingForm === DrawingForms.PENCIL ? 'active' : ''}`}
                    data-title="Pencil"
                    onClick={() => configure({ drawingForm: DrawingForms.PENCIL })}
                  >
                    <Pen2Logo />
                  </button>
                </div>
                <div className="wb-tools-wrapper">
                  <button
                    className={`wb pen-3 tip-bottom ${settings.drawingForm === DrawingForms.BRUSH ? 'active' : ''}`}
                    data-title="Brush"
                    onClick={() => configure({ drawingForm: DrawingForms.BRUSH })}
                  >
                    <Pen3Logo />
                  </button>
                </div>
              </>
            )
          }
          {
            selectedTool === ControlTools.TEXT && (
              <>
                <button
                  className={`wb b tip-bottom ${settings.bold ? 'active' : ''}`}
                  data-title="Bold"
                  onClick={() => configure({ bold: !settings.bold })}>
                  <BoldLogo />
                </button>
                <button
                  className={`wb b tip-bottom ${settings.italic ? 'active' : ''}`}
                  onClick={() => configure({ italic: !settings.italic })}
                  data-title="Italic">
                  <ItalicLogo />
                </button>
                {/* <button className="wb align tip-bottom" data-title="Align"><AlignLogo /></button>
                <button className="wb insert-link tip-bottom" data-title="Insert Link"><InsertLinkLogo /></button> */}
              </>
            )
          }
        </div>
        {
          hostMode && (
            <div className="whiteboard-undo-redo">
              <button className="wb undo tip-bottom" data-title="Undo" onClick={undo}>
                <UndoLogo />
              </button>
              <button className="wb redo tip-bottom" data-title="Redo" onClick={redo}>
                <RedoLogo />
              </button>
            </div>
          )
        }
        {
          closeWhiteboard && (
            <div className="whiteboard-actions">
              <button className="whiteboard-action" onClick={closeWhiteboard}>Close window</button>
            </div>
          )
        }
      </div>
      <div className={`whiteboard-tools ${transparent ? 'transparent hover:pointer-events-none' : ''} ${transparent && action == null ? 'hover:pointer-events-auto' : ''}`}>
        <div className="wb color-tooltip-wrapper">
          <div className="color-tooltip" style={{ backgroundColor: settings.strokeColor }}>
            <div className="wb-additional-wrapper">
              <div className="wb-additional">
                <StrokeWidthRange
                  defaultValue={settings.strokeWidth}
                  onChange={onWidthChange}
                />
                <ColorPicker />
              </div>
            </div>
          </div>
        </div>
        {
          hostMode && (
            <button className={`wb mouse ${selectedTool === ControlTools.MOUSE && 'active'}`} data-title="Mouse" onClick={onMouseLogoClick}>
              <MouseLogo />
            </button>
          )
        }
        <button className={`wb shape ${selectedTool === ControlTools.SHAPE && 'active'}`} data-title="Shape" onClick={onShapeLogoClick}>
          <ShapeLogo />
        </button>
        <button className={`wb eraser ${selectedTool === ControlTools.ERASER && 'active'}`} data-title="Eraser" onClick={onEraserLogoClick}>
          <EraserLogo color="#4F29EC" />
        </button>
        <button className={`wb pencil ${selectedTool === ControlTools.PENCIL && 'active'}`} data-title="Pencil" onClick={onPencilLogoClick}>
          <PencilLogo />
        </button>
        <button className={`wb text ${selectedTool === ControlTools.TEXT && 'active'}`} data-title="Text" onClick={onTextLogoClick}>
          <TextLogo />
        </button>
        {/* <button className="wb link" data-title="Link"><LinkLogo /></button> */}
        {/* <button className={`wb select ${selectedTool === ControlTools.SELECT && 'active'}`} data-title="Select" onClick={onSelectLogoClick}>
          <SelectLogo />
        </button> */}
      </div>
    </div>
  )
}

export default WhiteboardControls
