import getStroke from 'perfect-freehand'
import tinycolor from 'tinycolor2'

import { getSvgPathFromStroke } from './getSvgPathFromStroke'
import { DrawableElement, DrawingForms, Point } from '../types/typing'
import { getEllipseProperties } from './geometry'

// START BRUSH HELPERS
const varyColor = (sourceColor: string) => {
  const VARY_BRIGHTNESS = 5

  const amount = Math.round(Math.random() * 2 * VARY_BRIGHTNESS)
  const c = tinycolor(sourceColor)
  const varied =
    amount > VARY_BRIGHTNESS
      ? c.brighten(amount - VARY_BRIGHTNESS)
      : c.darken(amount)
  return varied.toHexString()
}

const makeBrush = (size: number, color: string) => {
  const brush = []
  let bristleCount = Math.round(size / 3)
  const gap = size / bristleCount
  for (let i = 0; i < bristleCount; i++) {
    const distance =
      i === 0 ? 0 : gap * i + (Math.random() * gap) / 2 - gap / 2
    brush.push({
      distance,
      thickness: Math.random() * 2 + 2,
      color: varyColor(color)
    })
  }
  return brush
}

const rotatePoint = (distance: number, angle: number | null, origin: Point) => [
  origin[0] + distance * Math.cos(angle as number),
  origin[1] + distance * Math.sin(angle as number)
]

const getBearing = (origin: ReturnType<typeof rotatePoint>, destination: ReturnType<typeof rotatePoint>) =>
  (Math.atan2(destination[1] - origin[1], destination[0] - origin[0]) -
    Math.PI / 2) %
  (Math.PI * 2)

const getNewAngle = (origin: ReturnType<typeof rotatePoint>, destination: ReturnType<typeof rotatePoint>, oldAngle: number | null) => {
  const bearing = getBearing(origin, destination)
  if (oldAngle == null) {
    return bearing
  }
  return oldAngle - angleDiff(oldAngle, bearing)
}

const angleDiff = (angleA: number, angleB: number) => {
  const twoPi = Math.PI * 2
  const diff =
    ((angleA - (angleB > 0 ? angleB : angleB + twoPi) + Math.PI) % twoPi) -
    Math.PI
  return diff < -Math.PI ? diff + twoPi : diff
}

const strokeBristle = (
  origin: ReturnType<typeof rotatePoint>,
  destination: ReturnType<typeof rotatePoint>,
  bristle: ReturnType<typeof makeBrush>[number],
  controlPoint: ReturnType<typeof rotatePoint>,
  ctx: CanvasRenderingContext2D,
  beingDrawn: boolean
) => {
  ctx.save()
  ctx.beginPath()
  ctx.moveTo(origin[0], origin[1])
  ctx.strokeStyle = bristle.color
  ctx.lineWidth = bristle.thickness
  // Optimizing to draw this computationally heavy visuals only after user interaction end
  if (!beingDrawn) {
    ctx.lineCap = 'round'
    ctx.lineJoin = 'round'
    ctx.shadowColor = bristle.color
    ctx.shadowBlur = bristle.thickness / 2
    ctx.quadraticCurveTo(
      controlPoint[0],
      controlPoint[1],
      destination[0],
      destination[1]
    )
  }
  ctx.lineTo(destination[0], destination[1])
  ctx.stroke()
  ctx.restore()
}
// END BRUSH HELPERS

// START POLYGON HELPERS
const getRoundedPoint = (x1: number, y1: number, x2: number, y2: number, radius: number, first: boolean) => {
  const total = Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2)),
    idx = first ? radius / total : (total - radius) / total
  return [x1 + (idx * (x2 - x1)), y1 + (idx * (y2 - y1))]
}

const getRoundedPoints = (pts: Point[], radius: number) => {
  let i1, i2, i3, p1, p2, p3, prevPt, nextPt,
    len = pts.length,
    res = new Array(len)
  for (i2 = 0; i2 < len; i2++) {
    i1 = i2 - 1
    i3 = i2 + 1
    if (i1 < 0) {
      i1 = len - 1
    }
    if (i3 == len) {
      i3 = 0
    }
    p1 = pts[i1]
    p2 = pts[i2]
    p3 = pts[i3]
    prevPt = getRoundedPoint(p1[0], p1[1], p2[0], p2[1], radius, false)
    nextPt = getRoundedPoint(p2[0], p2[1], p3[0], p3[1], radius, true)
    res[i2] = [prevPt[0], prevPt[1], p2[0], p2[1], nextPt[0], nextPt[1]]
  }
  return res
}

const drawPolygon = (ctx: CanvasRenderingContext2D, pts: Point[], radius: number) => {
  let roundedPoints: ReturnType<typeof getRoundedPoints> = []
  if (radius > 0) {
    roundedPoints = getRoundedPoints(pts, radius)
  }
  const points = roundedPoints?.length ? roundedPoints : pts
  let i, pt, len = points.length
  for (i = 0; i < len; i++) {
    pt = points[i]
    if (i == 0) {
      ctx.beginPath()
      ctx.moveTo(pt[0], pt[1])
    } else {
      ctx.lineTo(pt[0], pt[1])
    }
    if (radius > 0) {
      ctx.quadraticCurveTo(pt[2], pt[3], pt[4], pt[5])
    }
  }
  ctx.closePath()
}
// END POLYGON HELPERS

export const drawElementsOnCanvas = (elements: DrawableElement[], ctx: CanvasRenderingContext2D) => {
  const elementsBeingDrawn = elements[elements.length - 1]?.beingDrawn

  for (let index = 0; index < elements.length; index++) {
    const element = elements[index]

    switch (element.drawingForm) {
      case DrawingForms.PENCIL:
        {
          ctx.save()
          ctx.strokeStyle = element.settings.strokeColor
          ctx.lineWidth = element.settings.strokeWidth / 4
          ctx.lineCap = 'round'
          ctx.lineJoin = 'round'

          for (let index = 0; index < element.points.length; index++) {
            const point = element.points[index]

            ctx.beginPath()
            ctx.moveTo(point[0], point[1])

            const nextPoint = element.points[index + 1]
            if (nextPoint) {
              ctx.lineTo(nextPoint[0], nextPoint[1])
            }
            ctx.closePath()
            ctx.stroke()
          }
          ctx.restore()
          break
        }
      case DrawingForms.PEN:
      case DrawingForms.ERASER:
        {
          const stroke = getStroke(element.points, { size: element.settings.strokeWidth })
          const svgPath = getSvgPathFromStroke(stroke)

          ctx.save()
          ctx.fillStyle = element.settings.strokeColor
          ctx.fill(new Path2D(svgPath))
          ctx.restore()
          break
        }
      case DrawingForms.BRUSH: {
        const { points, settings: { strokeWidth, strokeColor } } = element
        const brush = makeBrush(strokeWidth, strokeColor)
        let angle: number | null = null,
          newAngle: number,
          prevPoint: Point,
          currentPoint: Point,
          bristle: ReturnType<typeof makeBrush>[number]

        for (let j = 0; j < points.length - 1; j++) {
          prevPoint = points[j]
          currentPoint = points[j + 1]
          newAngle = getNewAngle(prevPoint, currentPoint, angle)

          for (let i = 0; i < brush.length; i++) {
            bristle = brush[i]

            const bristleOrigin = rotatePoint(
              bristle.distance - strokeWidth / 2,
              angle,
              prevPoint
            )

            const bristleDestination = rotatePoint(
              bristle.distance - strokeWidth / 2,
              newAngle,
              currentPoint
            )
            const controlPoint = rotatePoint(
              bristle.distance - strokeWidth / 2,
              newAngle,
              prevPoint
            )

            strokeBristle(bristleOrigin, bristleDestination, bristle, controlPoint, ctx, elementsBeingDrawn)
          }

          angle = newAngle % (Math.PI * 2)
        }

        break
      }
      case DrawingForms.RECTANGLE: {
        ctx.save()
        ctx.lineWidth = element.settings.strokeWidth
        ctx.strokeStyle = element.settings.strokeColor
        drawPolygon(ctx, [
          [element.startX, element.startY],
          [element.endX, element.startY],
          [element.endX, element.endY],
          [element.startX, element.endY],
        ], 10)
        ctx.stroke()
        ctx.restore()
        break
      }
      case DrawingForms.TRIANGLE: {
        ctx.save()
        ctx.lineWidth = element.settings.strokeWidth
        ctx.strokeStyle = element.settings.strokeColor
        drawPolygon(ctx, [
          [element.startX, element.startY],
          [element.endX, element.endY],
          [element.startX - (element.endX - element.startX), element.endY],
        ], 10)
        ctx.stroke()
        ctx.restore()
        break
      }
      case DrawingForms.LINE: {
        ctx.save()
        ctx.lineWidth = element.settings.strokeWidth
        ctx.strokeStyle = element.settings.strokeColor
        drawPolygon(ctx, [
          [element.startX, element.startY],
          [element.endX, element.endY],
        ], 10)
        ctx.stroke()
        ctx.restore()
        break
      }
      case DrawingForms.CIRCLE: {
        const { center: [centerX, centerY], radiuses: [radius] } = getEllipseProperties(element)

        ctx.save()
        ctx.lineWidth = element.settings.strokeWidth
        ctx.strokeStyle = element.settings.strokeColor
        ctx.beginPath()
        ctx.ellipse(centerX, centerY, radius, radius, 0, 0, 2 * Math.PI)
        ctx.stroke()
        ctx.restore()
        break
      }
      case DrawingForms.ELLIPSE: {
        const { center: [centerX, centerY], radiuses: [radiusX, radiusY] } = getEllipseProperties(element)

        ctx.save()
        ctx.lineWidth = element.settings.strokeWidth
        ctx.strokeStyle = element.settings.strokeColor
        ctx.beginPath()
        ctx.ellipse(centerX, centerY, radiusX, radiusY, 0, 0, 2 * Math.PI)
        ctx.stroke()
        ctx.restore()
        break
      }
      case DrawingForms.TEXT: {
        const lineHeight = element.settings.lineHeight
        ctx.save()
        ctx.font = `${element.settings.bold ? 'bold' : ''} ${element.settings.italic ? 'italic' : ''} ${element.settings.fontSize}px ${element.settings.fontFamily}`
        ctx.fillStyle = element.settings.strokeColor
        ctx.textBaseline = 'top'

        const lines = element.text.split('\n')
        lines.forEach((line, index) => {
          ctx.fillText(line, element.startX, element.startY + index * lineHeight)
        })
        ctx.restore()

        break
      }
      default:
        break
    }
  }
}

export const drawDots = (ctx: CanvasRenderingContext2D) => {
  let r = 2,
    cw = 30,
    ch = 30

  const width = ctx.canvas.width
  const height = ctx.canvas.height

  for (let x = 20; x < width; x += cw) {
    for (let y = 20; y < height; y += ch) {
      ctx.fillStyle = '#b0b0b0'
      ctx.fillRect(x - r / 2, y - r / 2, r, r)
    }
  }
}

export const refreshKeyframe = (canvas: HTMLCanvasElement | null | undefined, text: string) => {
  const ctx = canvas?.getContext('2d')
  if (!ctx) {
    return
  }

  ctx.save()
  ctx.fillStyle = 'white'
  ctx.font = 'bold 10px arial'
  ctx.textBaseline = 'top'
  const textMeasurements = ctx.measureText(text)
  ctx.fillRect(0, 0, textMeasurements.width, 10)
  ctx.fillStyle = 'black'
  ctx.fillText(text, 0, 0)
  ctx.restore()
}
