import { Point, Vertices, DrawingForms, CanvasFigureElement } from '../types/typing'

export const resizeCanvasFigure = (vertex: Vertices, shift: Point, element: CanvasFigureElement): Point[] => {
  let drawingPoints: Point[] = [[0, 0]]
  const [shiftX, shiftY] = shift

  switch (vertex) {
    case Vertices.TOP_LEFT: {
      if (element.drawingForm === DrawingForms.TRIANGLE) {
        const shiftedStartX = element.startX + shiftX
        // Swap triangle bottom vertices if needed to reflect order from left to right
        return [
          [shiftedStartX, element.startY + shiftY],
          [Math.max(shiftedStartX - (element.endX - shiftedStartX), element.endX), element.endY],
        ]
      }

      return [
        [element.startX + shiftX, element.startY + shiftY],
        [element.endX, element.endY],
      ]
    }
    case Vertices.TOP_RIGHT: {
      return [
        [element.startX, element.startY + shiftY],
        [element.endX + shiftX, element.endY],
      ]
    }
    case Vertices.BOTTOM_LEFT: {
      if (element.drawingForm === DrawingForms.TRIANGLE) {
        const shiftedEndX = element.endX - shiftX
        const shiftedBottomLeftX = element.startX - (element.endX - element.startX) + shiftX

        return [
          [element.startX, element.startY],
          [Math.max(shiftedEndX, shiftedBottomLeftX), element.endY - shiftY],
        ]
      }

      return [
        [element.startX + shiftX, element.startY],
        [element.endX, element.endY + shiftY],
      ]
    }
    case Vertices.BOTTOM_RIGHT: {
      if (element.drawingForm === DrawingForms.TRIANGLE) {
        const shiftedEndX = element.endX + shiftX
        const shiftedBottomLeftX = element.startX - (element.endX - element.startX) - shiftX

        return [
          [element.startX, element.startY],
          [Math.max(shiftedEndX, shiftedBottomLeftX), element.endY + shiftY],
        ]
      }

      return [
        [element.startX, element.startY],
        [element.endX + shiftX, element.endY + shiftY],
      ]
    }
    default:
      return drawingPoints
  }
}
