/// <reference types="vite-plugin-svgr/client" />

import { useEffect, useMemo, useRef, useState } from 'react'

import WhiteboardControls from '../../components/WhiteboardControls'
import { DEFAULT_WHITEBOARD_HEIGHT, DEFAULT_WHITEBOARD_WIDTH, FRAME_RATE, defaultSettings } from '../../constants'
import { useDraw } from '../../hooks/useDraw'
import '../../index.css'
import { Actions, CursorMode, DrawableElement } from '../../types/typing'
import { getScaledElement } from '../../utils/generateElement'

type Props = {
  setCanvasStream?: (canvasStream: MediaStream) => void
  isActive: boolean
  receivedElement?: DrawableElement | null
  closeWhiteboard?: () => void
  darkMode?: boolean
  isInForeground?: boolean
  transparent?: boolean
  fullscreen?: boolean
}

const Whiteboard = ({ setCanvasStream, isActive, receivedElement: _receivedElement, closeWhiteboard, darkMode, isInForeground, transparent, fullscreen }: Props) => {
  const [whiteboardScale, setWhiteboardScale] = useState(1)
  const [whiteboardCanvas, setWhiteboardCanvas] = useState<HTMLCanvasElement>()

  const receivedElement = useMemo(() => _receivedElement && whiteboardCanvas ?
    getScaledElement(whiteboardScale, _receivedElement, whiteboardCanvas.getContext('2d') as CanvasRenderingContext2D)
    : _receivedElement,
    [whiteboardScale, _receivedElement, whiteboardCanvas]
  )

  const {
    init,
    onMouseDown,
    settings,
    configure,
    undo,
    redo,
    action,
    textareaRef,
    clear,
    canvas,
  } = useDraw({ settings: defaultSettings, receivedElement, transparent })

  const [stream, setStream] = useState<MediaStream>()

  const wrapperRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    clear()

    if (!canvas) {
      return
    }


    if (isActive) {
      // In case screen size is not large enough to accomodate default canvas dimensions

      // Suitable for window overlay apps
      if (fullscreen) {
        canvas.width = window.screen.width
        canvas.height = window.screen.height
        setWhiteboardScale(1)
      } else {
        // const onResize = () => {
        //   const canvasDimensions = canvas.getBoundingClientRect()

        //   if (wrapperRef.current) {
        //     const wrapperDimensions = wrapperRef.current.getBoundingClientRect()

        //     const scales = {
        //       width: wrapperDimensions.width / canvasDimensions.width,
        //       height: (wrapperDimensions.height - WHITEBOARD_CONTROL_BAR_HEIGHT) / canvasDimensions.height
        //     }
        //     const diffs = {
        //       width: wrapperDimensions.width - canvasDimensions.width,
        //       height: (wrapperDimensions.height - WHITEBOARD_CONTROL_BAR_HEIGHT) - canvasDimensions.height
        //     }

        //     // Whiteboard is overflowing from its container div
        //     if (scales.width < 1 || scales.height < 1) {
        //       if (scales.width < 1 && scales.height < 1) {
        //         // Which whiteboard dimension is less fitting. Resizing whiteboard according to it
        //         const dimension = scales.width < scales.height ? 'width' : 'height'
        //         const scale: number = scales[dimension]
        //         canvas.width = canvasDimensions.width * scale
        //         canvas.height = canvasDimensions.height * scale
        //       } else if (scales.width < 1) {
        //         canvas.width = canvasDimensions.width * scales.width
        //         canvas.height = canvasDimensions.height * scales.width
        //       } else if (scales.height < 1) {
        //         canvas.width = canvasDimensions.width * scales.height
        //         canvas.height = canvasDimensions.height * scales.height
        //       }
        //     }
        //     // Whiteboard wrapper div is larger or fitting default whiteboard size
        //     else if (wrapperDimensions.width >= DEFAULT_WHITEBOARD_WIDTH
        //       && (wrapperDimensions.height - WHITEBOARD_CONTROL_BAR_HEIGHT) >= DEFAULT_WHITEBOARD_HEIGHT
        //       && !(canvas.width === DEFAULT_WHITEBOARD_WIDTH && canvas.height === DEFAULT_WHITEBOARD_HEIGHT)
        //     ) {
        //       canvas.width = DEFAULT_WHITEBOARD_WIDTH
        //       canvas.height = DEFAULT_WHITEBOARD_HEIGHT
        //     }
        //     // Whiteboard is significantly smaller than its container
        //     else if ((Math.abs(diffs.width) >= 3 && canvasDimensions.width - canvas.height <= 1)
        //       || (Math.abs(diffs.height) >= 3 && canvasDimensions.height - canvas.height <= 1)
        //     ) {
        //       const dimension = scales.width < scales.height ? 'width' : 'height'
        //       const scale: number = scales[dimension]
        //       canvas.width = canvasDimensions.width * scale
        //       canvas.height = canvasDimensions.height * scale
        //     }

        //     // In any case, update scale number used to reposition received elements
        //     setWhiteboardScale(1)

        //     // Centering canvas inside div by keeping controller bar height in mind
        //     canvas.style.bottom = `${(wrapperDimensions.height - WHITEBOARD_CONTROL_BAR_HEIGHT - canvas.height) / 2}px`
        //   }
        // }

        // resizeObserver = new ResizeObserver(onResize)
        // if (wrapperRef.current) {
        //   resizeObserver.observe(wrapperRef.current)
        // }

        // onResize()
      }

      // Cleanup
      // return () => {
      //   resizeObserver?.disconnect()
      // }
    }

    // Preparing new stream for whiteboard activation and urging the component to clean previous stream in the following lines
    if (!isActive) {
      canvas.getContext('2d')
      setStream(canvas.captureStream(FRAME_RATE))
    }
  }, [isActive, canvas, fullscreen])

  useEffect(() => {
    if (stream?.active && setCanvasStream) {
      setCanvasStream(stream)
      stream.getVideoTracks()[0].onmute = () => {
        stream.getTracks().forEach((track) => track.stop())
      }
    }


    return () => {
      if (stream && (!stream.active || stream.getVideoTracks()[0].muted)) {
        stream.getTracks().forEach((track) => track.stop())
      }
    }
  }, [stream, setCanvasStream])

  const onSetCanvas = (canvas: HTMLCanvasElement) => {
    if (!canvas) {
      return
    }

    setWhiteboardCanvas(canvas)

    if (init) {
      init(canvas)
    }
  }

  return (
    <div className={`
    max-h-screen h-full w-full overflow-hidden absolute top-0 flex justify-center items-end
    ${!isActive ? 'hidden' : ''}
    ${isInForeground ? 'z-10' : ''}
    ${action !== null ? 'select-none' : ''}
    ${transparent ? 'bg-transparent' : 'bg-black'}
    ${action === Actions.MOVING ?
        'cursor-move'
        : action === Actions.RESIZING
          ? 'cursor-nwse-resize'
          : action === Actions.PANNING
            ? 'cursor-grabbing'
            : settings.cursorMode === CursorMode.SELECTION
              ? 'cursor-pointer'
              : ''}
    `}
      ref={wrapperRef}
    >
      <canvas
        onMouseDown={onMouseDown}
        ref={onSetCanvas}
        width={DEFAULT_WHITEBOARD_WIDTH}
        height={DEFAULT_WHITEBOARD_HEIGHT}
        className="relative"
      />
      <WhiteboardControls
        configure={configure}
        settings={settings}
        undo={undo}
        redo={redo}
        closeWhiteboard={closeWhiteboard}
        darkMode={darkMode}
        hostMode
        transparent={transparent}
        action={action}
      />
      <textarea
        ref={textareaRef}
        className="hidden fixed bg-gray-50 focus:bg-gray-100"
        id="text-input"
        style={{
          fontSize: `${settings.fontSize}px`,
          fontFamily: settings.fontFamily,
          lineHeight: `${settings.lineHeight}px`,
          color: settings.strokeColor,
        }}
      />
    </div>
  )
}

export default Whiteboard
