import { CanvasFigureElement, DrawingForms, Point } from '../types/typing'

export const distance = (a: Point, b: Point) => Math.sqrt(Math.pow(a[0] - b[0], 2) + Math.pow(a[1] - b[1], 2))

// checks if point c is on the connecting line of a and b points
export const isOnLine = (a: Point, b: Point, c: Point, allowedOffset: number): boolean => {
  const offset = distance(a, b) - (distance(a, c) + distance(b, c))

  return Math.abs(offset) < allowedOffset
}

export const getEllipseProperties = (element: CanvasFigureElement): { center: Point, radiuses: [number, number] } => {
  const outlinedRectLength = element.endX - element.startX // Preserving signs to determine center's direction from the initial point later
  const outlinedRectHeight = element.endY - element.startY

  const chosenDimension = Math.abs(outlinedRectLength) < Math.abs(outlinedRectHeight) ? outlinedRectLength : outlinedRectHeight

  let centerX = 0, centerY = 0, radiusX = 0, radiusY = 0

  if (element.drawingForm === DrawingForms.CIRCLE) {
    radiusX = Math.abs(chosenDimension) / 2
    radiusY = radiusX

    centerX = element.startX + Math.sign(outlinedRectLength) * radiusX
    centerY = element.startY + Math.sign(outlinedRectHeight) * radiusY

  } else if (element.drawingForm === DrawingForms.ELLIPSE) {
    radiusX = Math.abs(outlinedRectLength) / 2
    radiusY = Math.abs(outlinedRectHeight) / 2

    centerX = element.startX + outlinedRectLength / 2
    centerY = element.startY + outlinedRectHeight / 2
  }

  return { center: [centerX, centerY], radiuses: [radiusX, radiusY] }
}

export const isInsideEllipse = (point: Point, element: CanvasFigureElement) => {
  let { center: [centerX, centerY], radiuses: [radiusX, radiusY] } = getEllipseProperties(element)

  radiusX += element.settings.strokeWidth
  radiusY += element.settings.strokeWidth

  return Math.pow((point[0] - centerX), 2) / Math.pow(radiusX, 2)
    + Math.pow((point[1] - centerY), 2) / Math.pow(radiusY, 2) <= 1
}

export const isOnEllipse = (point: Point, element: CanvasFigureElement) => {
  const isInsideOuterEllipse = isInsideEllipse(point, element)
  const isInsideInnerEllipse = isInsideEllipse(point, { ...element, settings: { ...element.settings, strokeWidth: 0 } })

  // If the ellipse is on the stroke ring
  return isInsideOuterEllipse && !isInsideInnerEllipse
}
