export enum CursorMode {
  SELECTION,
  PAINT,
  ERASE,
}

export enum Actions {
  DRAWING,
  MOVING,
  RESIZING,
  PANNING,
  CREATING_TEXTBOX,
  ERASING,
}

export enum DrawingForms {
  LINE = 'LINE',
  RECTANGLE = 'RECTANGLE',
  ELLIPSE = 'ELLIPSE',
  TRIANGLE = 'TRIANGLE',
  CIRCLE = 'CIRCLE',
  PENCIL = 'PENCIL',
  PEN = 'PEN',
  BRUSH = 'BRUSH',
  ERASER = 'ERASER',
  TEXT = 'TEXT',
}

export enum Vertices {
  TOP_LEFT,
  TOP_RIGHT,
  BOTTOM_LEFT,
  BOTTOM_RIGHT,
}

export type Settings = {
  strokeWidth: number
  strokeColor: string
  drawingForm: DrawingForms
  cursorMode: CursorMode
  fontSize: number
  fontFamily: string
  lineHeight: number
  bold?: boolean
  italic?: boolean
}

export type DrawParams = {
  settings: Settings
  drawingPoints: Point[]
  id?: string | null
  shiftKey?: boolean
  drawingForm?: DrawingForms
  beingDrawn?: boolean
}

export type WriteParams = {
  id?: string
  startingPoint: Point
  text: string
  settings: Settings
  canvasContext: CanvasRenderingContext2D
  textarea?: HTMLTextAreaElement
}

type DrawableElementCommon = {
  beingDrawn: boolean
  settings: Settings
}

export type CanvasFigureElement = DrawableElementCommon & {
  id: string | null
  drawingForm: DrawingForms.RECTANGLE | DrawingForms.LINE | DrawingForms.TRIANGLE | DrawingForms.ELLIPSE | DrawingForms.CIRCLE
  startX: number
  startY: number
  endX: number
  endY: number
}

export type FreeFormElement = DrawableElementCommon & {
  id: string | null
  points: Point[]
  drawingForm: DrawingForms.PENCIL | DrawingForms.PEN | DrawingForms.BRUSH | DrawingForms.ERASER
}

export type TextElement = DrawableElementCommon & {
  id: string
  drawingForm: DrawingForms.TEXT,
  text: string
  startX: number
  startY: number
  endX: number
  endY: number
}

export type DrawableElement = CanvasFigureElement | FreeFormElement | TextElement

export type Point = [number, number]

export type Scale = { modifier: number, offset: Point }

export type WhiteboardProps = {
  settings: Settings
  transparent?: boolean
  panZoomDisabled?: boolean
  receivedElement?: DrawableElement | null
  scaleReturnedElement?: number
}

export enum ControlTools {
  MOUSE = 'MOUSE',
  SHAPE = 'SHAPE',
  ERASER = 'ERASER',
  PENCIL = 'PENCIL',
  TEXT = 'TEXT',
  LINK = 'LINK',
  SELECT = 'SELECT',
}

export enum Fonts {
  ARIAL = 'Arial',
  TIMES_NEW_ROMAN = 'Times New Roman',
  VERDANA = 'Verdana',
  COURIER_NEW = 'Courier New',
}
