// Returns true rendered dimensions of the played video inside it's element
export const getVideoDimensions = (video: HTMLVideoElement | null | undefined) => {
  if (!video) {
    return { width: 0, height: 0 }
  }

  // Ratio of the video's intrisic dimensions
  let videoRatio = video.videoWidth / video.videoHeight
  // The width and height of the video element
  let width = video.offsetWidth, height = video.offsetHeight
  // The ratio of the element's width to its height
  let elementRatio = width / height
  // If the video element is short and wide
  if (elementRatio > videoRatio) width = height * videoRatio
  // It must be tall and thin, or exactly equal to the original ratio
  else height = width / videoRatio
  return {
    width: width,
    height: height
  }
}
